@font-face {
  font-family: 'PostGrotesk-Book';
  src: url('/public/fonts/PostGrotesk-Book.otf') format('open-type'),
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
    font-family: 'PostGrotesk-Book', sans-serif;
}

.navbar {
    font-size: 20px;
    padding-bottom: 10px;
    box-shadow: 0 2px 2px #efefef;
}

.main-nav {
    list-style-type: none;
    display: none;
}

.nav-links,
.logo {
    text-decoration: none;
    color: #000;
}

a.nav-links {
    margin-top: 20px;
}

.main-nav li {
    text-align: center;
    margin: 15px auto;
    -webkit-animation-name: 'fade-in-list';
            animation-name: 'fade-in-list';
    opacity: 0;
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.logo {
    display: inline-block;
    font-size: 22px;
    margin-top: 10px;
    margin-left: 20px;
    -webkit-animation-name: 'fade-in-logo';
            animation-name: 'fade-in-logo';
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}


.logo img {
  height: 65px;
  margin: 10px 0;
}

.navbar-toggle {
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
    color: #4534e5;
    font-size: 35px;
}


.active {
    display: block;
    -webkit-animation-name: 'fade-in-menu';
            animation-name: 'fade-in-menu';
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
}

.close {
    display: none;
}

.dropdown .dropbtn {
  border: none;
  outline: none;
  color: #000;
  margin-left: 8px;
  box-shadow: none;
  padding: 0;
  font-family: 'PostGrotesk-Book', sans-serif; 
  font-size: 20px; 
}

.nav-links:hover,
.dropbtn:hover {
  color: #4534e5;
}

.fa-caret-down {
  padding-left: 5px;
}

.dropdown-content {
  display: none;
  background-color: #f9f9f9;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: center;
}

.dropdown-content a:hover {
  background-color: #4634e5d3;
  color: white;
}

.dropdown:hover .dropdown-content {
  display: block;
}


@media screen and (min-width: 888px) {
    .navbar {
        font-size: 17px;
        display: flex;
        justify-content: space-between;
        padding-bottom: 0;
        height: 100px;
        align-items: center;
    }

    .main-nav {
        display: flex;
        margin-right: 30px;
        flex-direction: row;
        justify-content: flex-end;
    }

    .main-nav li {
        margin: 0;
    }

    .nav-links {
        margin-left: 40px;
        -webkit-transition: all .4s;
        transition: all .4s;
    }

    .logo {
        margin-top: 0;
    }

    .navbar-toggle {
        display: none;
    }

  .dropdown-content {
    position: absolute;
    min-width: 130px;
  }

  .dropdown .dropbtn {
    font-size: 17px; 
    margin-left: 40px;
  }

}

@-webkit-keyframes fade-in-logo {
  0% {
    opacity: 0;
    margin-left: 50px;
  }

  100% {
    opacity: 1;
    margin-left: 20px;
  }
}

@keyframes fade-in-logo {
  0% {
    opacity: 0;
    margin-left: 50px;
  }

  100% {
    opacity: 1;
    margin-left: 20px;
  }
}

@-webkit-keyframes fade-in-menu {
  0% {
    height: 0;
  }

  100% {
    height: 250px;
  }
}

@keyframes fade-in-menu {
  0% {
    height: 0;
  }

  100% {
    height: 250px;
  }
}

@-webkit-keyframes fade-in-list {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fade-in-list {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}



.calendar-parent {
    margin: 24px;
}

.calendar-parent h1 {
    width: 80%;
    margin: 2px auto;
    margin-bottom: 14px;
    text-align: center;
}

.calendar-landing {
    display: grid;
    grid-template-columns: 75% 12px auto;
    width: 100%;
}

.calendar-header {
    display: flex;
    flex-direction: row;
}

.calendar-space {
    grid-column: 1 / 2;
}
.contact-space {
    grid-column: 3 / 4;
}

.contact-space h3 {
    text-align: center;
}

.single-contact {
    margin: 12px;
    padding: 8px 14px;
}

.single-contact div {
    margin: 5px auto;
}
.event-card {
    margin: 24px;
    padding: 12px;
}

.event-card h1 {
    text-align: center;
    margin-bottom: 18px;
}

.date-range {
    display: flex;
    flex-direction: row;
    justify-content: space-around;

}

.schedule, .event-contact {
    margin: 12px auto;
    padding: 12px 18px;
    background-color: rgb(236, 236, 236);
    border-radius: 4px;
}

